	module.exports = {"data":{"postsJson":{"title":"Миссия в Галилеи завершена","questionsTitle":"Изучение из прошлого ","display":"block","bookName":"От Луки","ref":"9:1-9:50","html":"<p>   <strong>9</strong><u>1</u>Созвав же двенадцать, дал силу и власть над всеми бесами и врачевать от болезней,<br />   <u>2</u>и послал их проповедывать Царствие Божие и исцелять больных.<br />   <u>3</u>И сказал им: </p><h1>Иисус</h1><h2>ничего не берите на дорогу: ни посоха, ни сумы, ни хлеба, ни серебра, и не имейте по две одежды;<br />   <u>4</u>и в какой дом войдете, там оставайтесь и оттуда отправляйтесь [в] [путь].<br />   <u>5</u>А если где не примут вас, то, выходя из того города, отрясите и прах от ног ваших во свидетельство на них.</h2><p>   <u>6</u>Они пошли и проходили по селениям, благовествуя и исцеляя повсюду.<br />   <u>7</u>Услышал Ирод четвертовластник о всём, что делал [Иисус], и недоумевал: ибо одни говорили, что это Иоанн восстал из мертвых;<br />   <u>8</u>другие, что Илия явился, а иные, что один из древних пророков воскрес.<br />   <u>9</u>И сказал Ирод: </p><h1>Ирод Антипа</h1><h4>Иоанна я обезглавил; кто же Этот, о Котором я слышу такое? </h4><p>И искал увидеть Его.<br />   <u>10</u>Апостолы, возвратившись, рассказали Ему, что они сделали; и Он, взяв их с Собою, удалился особо в пустое место, близ города, называемого Вифсаидою.<br />   <u>11</u>Но народ, узнав, пошел за Ним; и Он, приняв их, беседовал с ними о Царствии Божием и требовавших исцеления исцелял.<br />   <u>12</u>День же начал склоняться к вечеру. И, приступив к Нему, двенадцать говорили Ему: </p><h1>Ученики</h1><h3>отпусти народ, чтобы они пошли в окрестные селения и деревни ночевать и достали пищи; потому что мы здесь в пустом месте.</h3><p>   <u>13</u>Но Он сказал им: </p><h1>Иисус</h1><h2>вы дайте им есть. </h2><p>Они сказали: </p><h1>Ученики</h1><h3>у нас нет более пяти хлебов и двух рыб; разве нам пойти купить пищи для всех сих людей?</h3><p>   <u>14</u>Ибо их было около пяти тысяч человек. Но Он сказал ученикам Своим: </p><h1>Иисус</h1><h2>рассадите их рядами по пятидесяти.</h2><p>   <u>15</u>И сделали так, и рассадили всех.<br />   <u>16</u>Он же, взяв пять хлебов и две рыбы и воззрев на небо, благословил их, преломил и дал ученикам, чтобы раздать народу.<br />   <u>17</u>И ели, и насытились все; и оставшихся у них кусков набрано двенадцать коробов.<br />   <u>18</u>В одно время, когда Он молился в уединенном месте, и ученики были с Ним, Он спросил их: </p><h1>Иисус</h1><h2>за кого почитает Меня народ?</h2><p>   <u>19</u>Они сказали в ответ: </p><h1>Ученики</h1><h3>за Иоанна Крестителя, а иные за Илию; другие же [говорят], что один из древних пророков воскрес.</h3><p>   <u>20</u>Он же спросил их: </p><h1>Иисус</h1><h2>а вы за кого почитаете Меня? </h2><p>Отвечал Петр: </p><h1>Симен Петр</h1><h3>за Христа Божия.</h3><p>   <u>21</u>Но Он строго приказал им никому не говорить о сем,<br />   <u>22</u>сказав, </p><h1>Иисус</h1><h2>что Сыну Человеческому должно много пострадать, и быть отвержену старейшинами, первосвященниками и книжниками, и быть убиту, и в третий день воскреснуть.</h2><p>   <u>23</u>Ко всем же сказал: </p><h1>Иисус</h1><h2>если кто хочет идти за Мною, отвергнись себя, и возьми крест свой, и следуй за Мною.<br />   <u>24</u>Ибо кто хочет душу свою сберечь, тот потеряет ее; а кто потеряет душу свою ради Меня, тот сбережет ее.<br />   <u>25</u>Ибо что пользы человеку приобрести весь мир, а себя самого погубить или повредить себе?<br />   <u>26</u>Ибо кто постыдится Меня и Моих слов, того Сын Человеческий постыдится, когда приидет во славе Своей и Отца и святых Ангелов.<br />   <u>27</u>Говорю же вам истинно: есть некоторые из стоящих здесь, которые не вкусят смерти, как уже увидят Царствие Божие.</h2><p>   <u>28</u>После сих слов, дней через восемь, взяв Петра, Иоанна и Иакова, взошел Он на гору помолиться.<br />   <u>29</u>И когда молился, вид лица Его изменился, и одежда Его сделалась белою, блистающею.<br />   <u>30</u>И вот, два мужа беседовали с Ним, которые были Моисей и Илия;<br />   <u>31</u>явившись во славе, они говорили об исходе Его, который Ему надлежало совершить в Иерусалиме.<br />   <u>32</u>Петр же и бывшие с ним отягчены были сном; но, пробудившись, увидели славу Его и двух мужей, стоявших с Ним.<br />   <u>33</u>И когда они отходили от Него, сказал Петр Иисусу: </p><h1>Симен Петр</h1><h3>Наставник! хорошо нам здесь быть; сделаем три кущи: одну Тебе, одну Моисею и одну Илии, </h3><p>--не зная, что говорил.<br />   <u>34</u>Когда же он говорил это, явилось облако и осенило их; и устрашились, когда вошли в облако.<br />   <u>35</u>И был из облака глас, глаголющий: </p><h1>Бог</h1><h2>Сей есть Сын Мой Возлюбленный, Его слушайте.</h2><p>   <u>36</u>Когда был глас сей, остался Иисус один. И они умолчали, и никому не говорили в те дни о том, что видели.<br />   <u>37</u>В следующий же день, когда они сошли с горы, встретило Его много народа.<br />   <u>38</u>Вдруг некто из народа воскликнул: </p><h1>Отец Эпилептика</h1><h4>Учитель! умоляю Тебя взглянуть на сына моего, он один у меня:<br />   <u>39</u>его схватывает дух, и он внезапно вскрикивает, и терзает его, так что он испускает пену; и насилу отступает от него, измучив его.<br />   <u>40</u>Я просил учеников Твоих изгнать его, и они не могли.</h4><p>   <u>41</u>Иисус же, отвечая, сказал: </p><h1>Иисус</h1><h2>о, род неверный и развращенный! доколе буду с вами и буду терпеть вас? приведи сюда сына твоего.</h2><p>   <u>42</u>Когда же тот еще шел, бес поверг его и стал бить; но Иисус запретил нечистому духу, и исцелил отрока, и отдал его отцу его.<br />   <u>43</u>И все удивлялись величию Божию. Когда же все дивились всему, что творил Иисус, Он сказал ученикам Своим:</p><h1>Иисус</h1><h2>   <u>44</u>вложите вы себе в уши слова сии: Сын Человеческий будет предан в руки человеческие.</h2><p>   <u>45</u>Но они не поняли слова сего, и оно было закрыто от них, так что они не постигли его, а спросить Его о сем слове боялись.<br />   <u>46</u>Пришла же им мысль: кто бы из них был больше?<br />   <u>47</u>Иисус же, видя помышление сердца их, взяв дитя, поставил его пред Собою<br />   <u>48</u>и сказал им: </p><h1>Иисус</h1><h2>кто примет сие дитя во имя Мое, тот Меня принимает; а кто примет Меня, тот принимает Пославшего Меня; ибо кто из вас меньше всех, тот будет велик.</h2><p>   <u>49</u>При сем Иоанн сказал: </p><h1>Иоан ученик</h1><h3>Наставник! мы видели человека, именем Твоим изгоняющего бесов, и запретили ему, потому что он не ходит с нами.</h3><p>   <u>50</u>Иисус сказал ему: </p><h1>Иисус</h1><h2>не запрещайте, ибо кто не против вас, тот за вас.</h2>","path":"/330","Question1":"С каким характером вы больше всего идентифицируетесь в этом отрывке? Почему?","Question2":"Какие сильные и слабые стороны вы видите в этом персонаже в этом отрывке?","Question3":"Как вы думаете, чего Бог хотел сделать в жизни вашего выбранного персонажа?","Question4":"Как вы думаете, что Бог хочет делать в вашей жизни, когда вы размышляете над этим отрывком? Как вы можете сотрудничать с его любящими целями на этой неделе?","prevPath":"/329","nextPath":"/331"}},"pathContext":{}}

